<template>
  <div id="drop-area" ref="dropArea" :class="{ popout: isHovering }">
    <div class="information-text" :class="[ !hasData ? 'visible' : true ]">
      Please drag and drop a text file onto this space.
    </div>
    <div 
      class="data-text" 
      ref="fileContent"
      :class="{ visible: hasData }"
    >
      <p class="typewriter">
        <span class="left-title">Most frequent word: "{{ mostFrequent.word }}" ({{ mostFrequent.number }} times)</span>
        <span class="right-title" @click="reset">Click here to reset</span>
        <br /><br />
        {{ data }}
      </p>
    </div>
  </div>
</template>

<script>
export default {
  name: 'DropArea',

  data() {
    return {
      dropArea: null,
      hasData: false,
      isHovering: false,
      data: '',
    }
  },

  computed: {
    mostFrequent: function() {
      if(this.data == '') {
        return { word: '', number: 0 }
      }

      // First we replace whitespace with normal spaces (e.g. \n to a space) and then we only allow words
      const dataCopy = this.data.replace(/\s/g, ' ').replace(/[^A-Za-z0-9 ]/g,'').toLowerCase();
      const words = dataCopy.split(' ');

      // Create a temporary k,v object
      var list = {};

      console.time("occurrences")

      words.forEach(word => {
        // Make sure we don't count empty characters
        if(word == '') return;
        
        if(!list[word]) {
          list[word] = 0;
        }

        list[word]++;
      })

      console.log("Time after adding words and occurences to a dictionary")
      console.timeLog("occurrences");

      // Calculate most frequent word and times its been used
      var _mostFrequentWord = '';
      var _mostFrequentWordNumber = 0;

      for (var item in list) {
        if(list[item] > _mostFrequentWordNumber) {
          _mostFrequentWordNumber = list[item];
          _mostFrequentWord = item; 
        }
      }

      console.log("Time after sorting")
      console.timeEnd("occurrences");
      return { word: _mostFrequentWord, number: _mostFrequentWordNumber }
    }
  },
  
  methods: {
    preventDefaults: function(e) {
      e.preventDefault()
      e.stopPropagation()
    },
    reset: function() {
      this.addDragAndDropEventListeners();
      this.data = '';
      this.hasData = false;
      this.isHovering = false;
    },
    onDragEnter: function() {
      this.isHovering = true;
    },
    onDragLeave: function() {
      this.isHovering = false;
    },
    onDrop: function(e) {
      this.removeDragAndDropEventListeners();
      this.isHovering = false;

      let files = e.dataTransfer.files;
      console.log(files);
      
      // Read all files uploaded
      files.forEach(file => {
        this.getFileDataAsString(file);
      })
    },
    getFileDataAsString: function(file) {
      var fileReader = new FileReader();
      var self = this;

      fileReader.onload = function() {
        self.hasData = true;
        self.data += fileReader.result;
      }
      
      fileReader.readAsBinaryString(file);
    },
    addDragAndDropEventListeners() {
      this.dropArea.addEventListener('dragleave', this.onDragLeave, false);
      this.dropArea.addEventListener('dragenter', this.onDragEnter, false);
      this.dropArea.addEventListener('drop',      this.onDrop,      false);

      ['dragenter', 'dragover', 'dragleave', 'drop'].forEach(eventName => {
        this.dropArea.addEventListener(eventName, this.preventDefaults, false)
      })
    },
    removeDragAndDropEventListeners() {
      this.dropArea.removeEventListener('dragleave', this.onDragLeave, false);
      this.dropArea.removeEventListener('dragenter', this.onDragEnter, false);
      this.dropArea.removeEventListener('drop',      this.onDrop,      false);
    }
  },

  mounted() {
    this.dropArea = this.$refs.dropArea;

    this.addDragAndDropEventListeners();
  }
}
</script>

<style scoped>
.popout {
    animation: popout 0.5s ease;
    -webkit-animation: popout 0.5s ease;
    animation-fill-mode: forwards;

}
@keyframes popout {
    from{transform:scale(1)}
    to{transform:scale(1.05)}
}
@-webkit-keyframes popout {
    from{-webkit-transform:scale(1)}
    to{-webkit-transform:scale(1.05)}
}

.data-text {
  height: 420px;
  font-size: 14px;
  color: #EECD86;
  display: none;
  letter-spacing: .15em;
  padding: 25px;
  font-weight: 300;
  text-align: left;
  overflow-y: scroll;
  overflow-x: auto;
  width: 100%;
}

::-webkit-scrollbar {
  width: 5px;
}

::-webkit-scrollbar-thumb {
  background: #EECD86;
}

.information-text {
  text-align: center;
  color: #EECD86;
  font-size: 24px;
  display: none;
}

.right-title {
  float: right; 
  cursor: pointer; 
  color: #B95835;
  text-decoration: underline;
}

.left-title {
  font-size: 24px;
  color: #B95835;
}

.visible { 
  display: block;
}

#drop-area {
  margin: 0 auto;
  background-image: url("data:image/svg+xml,%3csvg width='100%25' height='100%25' xmlns='http://www.w3.org/2000/svg'%3e%3crect width='100%25' height='100%25' fill='none' rx='15' ry='15' stroke='%23B95835' stroke-width='10' stroke-dasharray='50%2c 25' stroke-dashoffset='0' stroke-linecap='square'/%3e%3c/svg%3e");
  border-radius: 15px;
  width: 50%;
  height: 500px;
  box-shadow: 2px 2px 2px 2px #333;
  background-color: #3D3242;
  display: flex;
  align-items: center;
  justify-content: center;
}
</style>